/*--------------------------------------
    elements
--------------------------------------*/

body {
    font-family: 'Montserrat', sans-serif;
    background: #F4F4F4;
    font-size: 16px;
}

span,
p,
input {
    line-height: 26px;
}

p {
    margin-bottom: 25px
}

img {
    max-width: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: 600;
    margin-top: 0;
    margin-bottom: 25px
    
}

h5 {
    font-size: 1.6rem;
}

a {
    text-decoration: none;
    display: inline-block;
    color: $primary-color;
}

a:hover {
	color: $dark;
}

ul {
    padding: 0;
    margin: 0;
    list-style-type: none;
    li {
        margin-bottom: 25px;
    }
}


.fa-heart {
    color: $sidebar-heart;
}

.fa-leaf {
    color: $dark-slate-green;
}

li span, p span {
    position: relative;
    left: 10px;
}

.media .media-left {
    float: left;
}

.form-control {
    height: 5.2rem;
}


	


/*-------------------
header
---------------------*/


// CTA button
.subscribe-form {
	margin: 20px 0;
}

.input-group {
	position: relative;
	display: table;
	border-collapse: separate;
}

.input-group .form-control {
	font-size: 1.5rem;
	border-radius: 25px;
}

.subscribe-form .input-group-btn .btn {
	font-size: 1.6rem;
	padding-left: 30px;
  padding-right: 30px;
  -webkit-border-radius: 0 25px 25px 0;
  -moz-border-radius: 0 25px 25px 0;
  -ms-border-radius: 0 25px 25px 0;
  -o-border-radius: 0 25px 25px 0;
  border-radius: 0 25px 25px 0;
}


.btn-theme {
	color: #fff;
	background-color: $primary-color;
    &:hover {
        background-color: $dark;
        color: #fff;
    }
}

.jumbotron.home-page p {
	color: white;
	font-size: 1.5rem;
}






