/* ===============
post css
 ===================*/

.main-content {
    margin-top: 50px;
}

article {
    border: 1px solid #eee;
}

.entry-header {
    margin: 40px 0 23px;
}

.entry-header h1 a,
.entry-header h6 a {
    text-decoration: none;
    letter-spacing: 0.5px;
}

.entry-header h1 {
    margin-top: 0;
    font-size: 24px;
}


.entry-header h6 a {
    color: #00BFF3;
    font-weight: 700;
    font-size: 12px;
}

.entry-header h1 a {
    font-weight: 600;
    color: #333333;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
    font-size: 24px;
    line-height: 34px;
}

.entry-header h1 a:hover {
    color: #00acdf;
}

.post-thumb img {
    width: 100%;
}

a.more-link {
    font-size: 12px;
    letter-spacing: 1px;
    overflow: hidden;
    padding: 10px 20px;
    text-decoration: none;
    margin: 24px 0 28px;
    display: inline-block;
    color: #00BFF3;
    border: 1px solid #eee;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
    background-color: white;
}

a.more-link:hover {
    background: #00BFF3;
    color: #fff;
    border-color: #00BFF3;
}

.post {
    background: #fff;
    margin-bottom: 50px;
}

.post-content {
    padding: 0 43px 30px 43px;
    overflow: hidden;
    h6 {
        color: $primary-color;
    }
}

.post .social-share {
    border-top: 1px solid #EEE;
    padding: 15px 0 75px;
}

.social-share span a {
    color: #333;
}

.social-share-title {
    padding: 9px 0;
    font-style: italic;
    font-size: 13px;
}

.social-share span a:hover {
    color: #00BFF3;
    text-decoration: none;
}

.post-thumb {
    position: relative;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-direction: column;
    -ms-flex-direction: column;
    flex-direction: column;
}

.post-thumb:hover .post-thumb-overlay {
    opacity: 1;
}

.post-thumb-overlay {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    height: 100%;
    width: 100%;
    background: rgba(0, 0, 0, 0.4);
    opacity: 0;
    overflow: hidden;
    -webkit-transition: all .5s;
    transition: all .5s;
}

.post-thumb-overlay div {
    color: #fff;
    display: inline-block;
    height: 40px;
    width: 132px;
    border: 1px solid #fff;
    line-height: 40px;
    text-decoration: none;
    font-weight: 600;
    font-size: 14px;
    position: relative;
    top: 50%;
    letter-spacing: 1px;
    margin: 5px;
    margin-top: -18px;
    text-transform: uppercase;
}


/* ===========
pagination
==============*/

.pagination {
    display: inline-block;
    padding-left: 0;
    margin: 0 0 50px;
    border-radius: 4px;
}

.pagination>li>a,
.pagination>li>span {
    position: relative;
    float: left;
    padding: 6px 12px;
    margin-left: 5px;
    line-height: 1.42857143;
    color: #333;
    text-decoration: none;
    background-color: #fff;
    border: 0;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
    // font-weight: 600;
}

.pagination>li:first-child>a,
.pagination>li:first-child>span {
    margin-left: 0;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
}

.pagination>li>a:focus,
.pagination>li>a:hover,
.pagination>li>span:focus,
.pagination>li>span:hover {
    z-index: 2;
    color: #ffffff;
    background-color: #00BFF3;
}

.pagination>li:last-child>a,
.pagination>li:last-child>span {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
}

.pagination>.active>a,
.pagination>.active>a:focus,
.pagination>.active>a:hover,
.pagination>.active>span,
.pagination>.active>span:focus,
.pagination>.active>span:hover {
    z-index: 3;
    color: #fff;
    background-color: #00BFF3;
}


