}

.about-me-img {
    position: relative;
    z-index: 2;
}



.about-me-content h3 {
    font-size: 18px;
    letter-spacing: 0.5px;
    font-weight: 600;
    margin-top: 25px;
    font-family: 'Open Sans', sans-serif;
}

.img-me {
    border: 5px solid #fff;
