}


/*=============
subscribe newsletter
================*/

.search-bar input[type="text"] {
    width: 100%;
    margin-bottom: 14px;
    border: 1px solid #eeeeee;
    padding: 5px 15px;
    font-style: italic;
    border-radius: 0;
    background: #fafafa;
}

.btn-subscribe {
    width: 100%;
    background-color: #333333;
    color: #fff;
    font-weight: 600;
    border-radius: 0;
    -webkit-transition: all 0.33s;
    transition: all 0.33s;
    letter-spacing: 1px;
    font-size: 12px;
    padding: 9px 0;
    font-family: 'Open Sans', sans-serif;
}

.btn-subscribe:hover {
    background-color: #00BFF3;
    color: #fff;
}

.widget-title {
    font-size: 18px;
    margin: 0 0 25px;
    letter-spacing: 0.5px;
    color: #333333;
}


/*============
popular post
=============*/

.popular-post {
    border-bottom: 1px solid #eee;
    padding: 25px 0 20px;
}

.popular-post:last-child {
    border-bottom: 0 solid #eee;
    padding-bottom: 0;
}

.widget .popular-post:first-of-type {
    padding-top: 0;
}

.p-content a {
    font-size: 12px;
    color: #333333;
    text-decoration: none;
    display: block;
    font-weight: 600;
    transition: all 0.44s;
}

.p-content a:hover {
    color: #00BFF3
}

.p-content span {
    color: #666666;
    font-size: 12px;
    font-style: italic;
}

.popular-img img {
    width: 100%;
}

.popular-img {
    position: relative;
    width: 100%;
    margin-bottom: 15px;
}

.media .popular-img {
    position: relative;
    width: 100%;
    margin-bottom: 15px;
}

.popular-img:hover .p-overlay {
    opacity: 1;
}

.p-overlay {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.5);
    opacity: 0;
    -webkit-transition: all 0.44s;
    transition: all 0.44s;
}


/*=====
feature post
=======*/

#widget-feature {
    overflow: hidden;
}

.feature-content {
    position: relative;
    overflow: hidden;
}

.feature-content img {
    width: 100%;
}

.feature-content h5 {
    font-size: 16px;
    padding-bottom: 10px;
    letter-spacing: .5px;
}

.overlay-text {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.5);
    opacity: 1;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-direction: column;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-transition: all 0.44s;
    transition: all 0.44s;
    color: #fff;
}

.feature-content:hover .overlay-text {
    color: #fff;
    text-decoration: none;
}


/*==================
owl carousel
==============*/

#widget-feature:hover .owl-controls .owl-buttons .owl-prev {
    left: 10px;
}

#widget-feature .owl-controls .owl-buttons .owl-prev {
    left: -50px;
}

#widget-feature:hover .owl-controls .owl-buttons .owl-next {
    right: 10px;
}

#widget-feature .owl-controls .owl-buttons .owl-next {
    right: -50px;
}

#widget-feature .owl-controls .owl-buttons div {
    background: none;
    border-radius: 0;
    line-height: 30px;
    margin: 0;
    padding: 0;
    font-size: 30px;
    position: absolute;
    top: 50%;
    margin-top: -15px;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}


