.entry-content {
    ul {
        padding-left: 4rem;
        li {
            padding-bottom: 1rem;
        }
    }
}

.entry-header a {
    text-align: center;
}

.post-area {
    font-size: 1.6rem; 
    // line-height: 3.5rem;
    ul {
        padding: 0 0 0 30px;
        margin: 0;
    }
}

.testimonials-header, .post-content, .work-with-me-headers, .category-title {
    font-size: 1.6rem;
        h1 { // ,h3
   font-family: 'Libre Baskerville', serif;
    }
    h1::after{ // ,h3::after
			border-bottom: 6px solid $primary-color;
			content: "";
			display: block;
			margin: 0 auto 40px;
			padding-bottom: 10px;
			width: 20%;
    }
}

.category-title {
	padding-top: 40px;
}


.decoration a {
    color: #60c7eb;
    border: 1px solid #60c7eb;
    font-size: 14px;
    // height: 35px;
    // width: 105px;
    // line-height: 35px;
    padding: 10px;
    border-radius: 0;
    margin: 30px 6px 30px 0;
}

.decoration a:hover {
    color: #fff;
    border: 1px solid #60c7eb;
    background-color: #60c7eb;
}

.top-comment {
    padding: 30px;
    background-color: #fff;
    color: #212121;
    margin-bottom: 60px;
}

.top-comment img {
    padding-right: 15px;
}

.top-comment p {
    line-height: 24px;
}

.single-blog-box {
    position: relative;
    text-align: center;
    margin: 30px 0;
}


/*single blog*/

.single-blog-box .overlay {
    background-color: rgba(0, 0, 0, 0.2);
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
}

.single-blog-box .promo-text {
    font-weight: 500;
    position: absolute;
    top: 10%;
    left: 5%;
    margin: 0;
    border: 1px solid transparent;
    width: 90%;
    height: 80%;
    background: rgba(0, 0, 0, 0.0);
    color: #fff;
    -webkit-transition: all .5s;
    transition: all .5s;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-direction: column;
    -ms-flex-direction: column;
    flex-direction: column;
}

.single-blog-box .promo-text p {
    color: #fff;
}

.single-blog-box .promo-text i {
    padding: 0 5px;
    font-size: 30px;
    margin-bottom: -35px;
}

.single-blog-box .promo-text h5 {
    color: #fff;
    margin-top: 0;
}

.single-blog-box:hover .promo-text {
    border: 1px solid #fff;
    width: 90%;
    height: 80%;
    background: rgba(0, 0, 0, 0.3);
}


/*related post section*/

.related-post-carousel {
    padding: 20px 20px 10px;
    margin-top: 20px;
    background-color: #fff;
    color: #212121;
}

.related-heading {
    font-size: 14px;
    text-transform: uppercase;
    letter-spacing: 1px;
    color: #212121;
    font-weight: 700;
}

.single-item p {
    text-align: center;
    padding-bottom: 0;
}

.related-post-carousel .owl-buttons {
    position: absolute;
    top: -45px;
    right: 12px;
}

.related-post-carousel .owl-theme .owl-controls .owl-buttons div {
    color: #333;
    margin: 5px;
    padding: 0 5px;
    font-size: 22px;
    background: transparent;
}

.single-item p {
    text-align: center;
    padding-bottom: 0;
}

.single-item p {
    text-align: center;
    padding-bottom: 0;
    color: #444444;
}

.bottom-comment {
    background: #fff;
    padding: 20px;
    margin: 50px 0;
    color: #212121;
    overflow: hidden;
}

.bottom-comment .comment-text {
    padding: 2px;
}

.bottom-comment h4 {
    text-transform: uppercase;
    font-size: 14px;
    letter-spacing: 1px;
    font-weight: bold;
    margin-bottom: 10px;
}

.bottom-comment .comment-img {
    padding: 10px 15px 100px;
    float: left;
}

.bottom-comment h5 {
    font-size: 12px;
    letter-spacing: 1px;
    text-transform: uppercase;
    margin-bottom: 5px;
    font-weight: bold;
}

.bottom-comment p.comment-date {
    color: #888888;
}

.bottom-comment p.para {
    color: #212121;
    font-size: 13px;
    line-height: 24px;
    margin-bottom: 5px;
}

.bottom-comment a.replay {
    text-transform: uppercase;
    color: #212121;
    background: #eee;
}

.bottom-comment a.replay:hover {
    background-color: #ddd;
}

.leave-comment {
    background-color: #fff;
    margin: 30px 0;
    padding: 20px;
    color: #212121;
}

.leave-comment h4 {
    font-size: 14px;
    text-transform: uppercase;
    font-weight: 700;
}

.leave-comment input,
.leave-comment textarea {
    background-color: #FAFAFA;
    color: #999999;
    border-radius: 0;
    font-size: 14px;
    line-height: 28px;
    padding: 20px;
    border-color: #eee;
    -webkit-box-shadow: inset 0 0 0 rgba(0, 0, 0, .075);
    box-shadow: inset 0 0 0 rgba(0, 0, 0, .075);
}

.leave-comment input[type="text"]+input[type="email"] {
    border: 1px solid #eeeeee;
    font-style: italic;
}

input[type="text"]:focus,
input[type="email"]:focus,
textarea {
    border-color: #eee;
    outline: none;
    border: 1px solid;
}

.form-control:focus {
    border-color: #eee;
    outline: 0;
    box-shadow: inset 0 0 0 rgba(0, 0, 0, .075), 0 0 0 rgba(102, 175, 233, .6);
}

.send-btn {
    background: #333;
    color: #fff;
    text-transform: uppercase;
    -webkit-transition: all .33s;
    transition: all .33s;
    border-radius: 0;
}

.send-btn:hover {
    background: #00BFF3;
    color: #fff;
}


/*=== coming soon ===*/

.coming-soon-overlay {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.55);
}

.coming-soon {
    background: url("assets/images/coming-soon.jpg") no-repeat;
    background-size: cover;
    position: relative;
    height: 100%;
    width: 100%;
    overflow: hidden;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-direction: column;
    -ms-flex-direction: column;
    flex-direction: column;
}

.coming-soon h4 {
    color: #fff;
    letter-spacing: 1px;
    font-size: 30px;
}

.coming-soon h3 {
    color: #fff;
    letter-spacing: 1px;
    font-size: 30px;
    padding-bottom: 30px;
}

.coming-soon p {
    color: #fff;
    font-size: 18px;
    padding-bottom: 20px;
}


/*=======
post grid
=========*/

.post-grid .post-content {
    padding: 0 30px;
    overflow: hidden;
}

.post-grid .entry-header {
    margin: 31px 0 20px;
}

.post-grid .entry-header h1,
.post-list .entry-header h1 {
    margin: 0;
    line-height: 24px;
}

.post-grid .entry-header h1 a,
.post-list .entry-header h1 a {
    font-size: 18px;
    line-height: 24px;
}

.post-grid .social-share,
.post-list .social-share {
    border: 0;
    padding: 0 0 40px;
}

.post-grid .social-share-title {
    padding: 10px 0 30px;
    font-style: italic;
    font-size: 13px;
}

.post-list .entry-header {
    margin: 22px 0;
}

