/*==========
about me
============*/

.about-me-text {
    padding: 40px 0;
    border-bottom: 1px solid #eee;
}

.about-me-text p {
    margin-bottom: 0;
}

.single-about {
    padding: 40px 0;
}

.single-about:last-child {
    padding: 0;
}

.about-us-img {
    padding-right: 30px;
    padding-bottom: 30px;
}


.about-us-text h4 {
    letter-spacing: 1px;
    font-weight: 500;
    font-size: 18px;
    line-height: 28px;
    color: #444444;
    margin-bottom: 5px;
}

.about-us-text h5 {
    font-style: italic;
    font-size: 14px;
    line-height: 28px;
    letter-spacing: 0.5px;
    font-weight: normal;
    padding-bottom: 15px;
}

.about-us-text p {
    line-height: 28px;
}

.progress {
    height: 24px;
    margin-bottom: 20px;
    overflow: hidden;
    background-color: #f5f5f5;
    border-radius: 2px;
    -webkit-box-shadow: inset 0 1px 2px rgba(0, 0, 0, .1);
    box-shadow: inset 0 1px 2px rgba(0, 0, 0, .1);
}

.progress-bar {
    float: left;
    width: 0;
    padding-left: 10px;
    height: 100%;
    font-size: 13px;
    line-height: 24px;
    color: #fff;
    text-align: left;
    background-color: #00BFF3;
    -webkit-box-shadow: inset 0 -1px 0 rgba(0, 0, 0, .15);
    box-shadow: inset 0 -1px 0 rgba(0, 0, 0, .15);
    -webkit-transition: width .6s ease;
    -o-transition: width .6s ease;
    transition: width .6s ease;
}

h3.bar-heading.text-uppercase {
    padding: 0 0 10px;
}


/*======
about us one
========*/

.about-p {
    padding-bottom: 40px;
    border-bottom: 1px solid #eee;
}

.text-ltr {
    direction: rtl;
    margin-right: 20px;
}

.ml30 {
    margin-left: 30px;
}

.mr0 {
    margin-top: 0 !important;
}


/*========
