    margin-top: -18px;
    text-transform: uppercase;
}

.portfolio-2 .post-thumb-overlay div {
    height: 34px;
    width: 124px;
    border: 1px solid #fff;
    line-height: 34px;
    text-decoration: none;
    font-weight: 500;
    font-size: 12px;
}

.portfolio-2.post-grid .entry-header {
    margin: 20px 0 20px;
}

.portfolio-2.post {
    background: #fff;
    margin-bottom: 30px;
}

.portfolio-title {
    margin-bottom: 50px;
    margin-top: 0;
    font-size: 24px;
    letter-spacing: 0.5px;
}


/*========
header one
==========*/

.header-logo {
    margin-top: 30px;
}


/*========
header two
==========*/

.header-bg {
    background: url("assets/images/header-3-bg.jpg") no-repeat scroll 0 0;
    background-size: cover;
}

.logo2 {
    position: relative;
    z-index: 1;
    padding: 60px 0;
}

.logo2:after {
    position: absolute;
    top: 0;
    left: 0;
    background: rgba(51, 51, 51, 0.1);
    height: 100%;
    width: 100%;
    z-index: -1;
    content: "";
}


/* 404 page */

.st-content {
    margin: 50px 0 50px;
}

.white-color {
    background-color: #fff;
    box-shadow: 0 0 1px rgba(0, 0, 0, 0.15);
}

.padding-content {
    padding: 40px;
}

.error-404 {
    padding-top: 100px;
    padding-bottom: 150px;
}

.error-404 h1 {
    font-size: 150px;
    margin-bottom: 30px;
}

.st-content #searchform input#s {
    padding: 10px 13px;
    border-radius: 0;
    width: 100%;
}

p.go-back-home {
    margin: 40px;
    color: #eee;
    text-align: center;
}

p.go-back-home a {
    font-size: 12px;
    letter-spacing: 1px;
    overflow: hidden;
    padding: 10px 20px;
    text-decoration: none;
    margin: 24px 0 28px;
    display: inline-block;
    color: #00BFF3;
    border: 1px solid #eee;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
}

p.go-back-home a:hover {
    background: #00BFF3;
    color: #fff;
    border-color: #00BFF3;
}

